.item {
 border-bottom: 1px solid #E5E5E5;
}

.item:last-of-type {
    border-bottom: none;
}

.users {
    width: 100%;
    border-collapse: collapse;
}

.buttonsContainer {
    display: flex;
    justify-content: right;
    margin-bottom: 18px;
}
.buttonsContainer button:nth-last-of-type(2){
    margin: 0 12px;
}
.container {
    max-width: 606px;
    width: 100%;
}

.thead {
    background: #E5E5E5;
    font-size: 14px;
    font-weight: 600;
}

.nameCol {
   width: 50%;
    padding: 11px 0px 9px 17px;
}

.ageCol {
    width: 50%;
}