.sendForm {
    display: flex;
    justify-content: space-between;
    padding-top: 68px;
    padding-bottom: 32px;
}

.textarea {
    width: 976px;
    height: 40px;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    resize:none
}

.button {
    padding: 10px 39px;
    background: #0066CC;
    border: none;
    border-radius: 20px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}

